@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* Hamburger Menu */
input[type='checkbox'] {
  display: none;
}

#home #main-nav {
  z-index: 10;
}
/* Media Queries */
@media (max-width: 720px) {
  #main-nav nav,
  #main-nav nav ul{
    flex-direction: column;
  }

  nav .menu li {
    margin-left: 15px;
    margin-right: 30px;
  }
  
  .menu {
    display: none;
    margin-bottom: 50px;
  }
  

  input[type='checkbox']:checked + .menu {
    display: block;
  }

  .hamburger-menu {
    position:absolute;
    top:25px;
    right:35px;
  }

  .hamburger-menu .bar {
    width: 30px;
    height: 3px;
    background-color: white;
    margin: 6px 0;
  }

  #home .Home-headerLogo {
    width: 50%;
  }

  #events .Speaker-wrapper {
    flex-direction: column;
    text-align: center;
  }

  #events .Speaker-copy {
    padding-left: 0px;
  }

  #contact {
    height: auto;
  }
  #contact .Contact-innerWrapper {
    text-align: center;
  }

  #contact .Contact-footer {
    flex-direction: column;
  }
  #contact .Contact-social {
    margin-top: 10px;
    padding: 0px 20px;
  }
  #contact .Contact-footerLogo {
    margin: 50px 0px;
    width: 40%;
  }

  #sponsors .Sponsors-sponsorLogo {
    margin-top: 50px;
    width: 80%;
  }
}

@media (max-width: 970px) {
  nav p{
    margin-left: 15px;
    margin-right: 30px;
  }
  #home .Home-copy-2 {
    text-align: center;
  }

  #main-nav .menu {
    margin-right: 15px;
  }
  #event-card {
    max-width: calc(100% - 90px);
  }



  #events .EventCard-copyWrapper {
    flex-direction: column;
  }
  #events .EventCard-copy {
    width: 100%;
  }
  #events .EventCard-speakerSet {
    padding-left: 0;
    padding-top: 30px;
  }
  #partner {
    height: auto;
  }
  #partner .Partner-innerWrapper {
    flex-direction: column;
  }
  #partner .PartnerCard-card {   
    margin: 30px auto;
    width: calc( 100% - 60px);
    max-width: 100%;

  }
}

@media (min-width: 970px) {
  #event-card{
    max-width: 990px !important;
  }

  #events .Speaker-wrapper:nth-of-type(2) .Speaker-copy {
    display: flex;
    align-items: center;
  }

  #partner .PartnerCard-card {   
    margin: auto;
    max-width: 30%;
  }
}

#events{
  margin-left: 15px;
  margin-right: 15px;
}

#about .About-copy,
#sponsors .Sponsors-innerWrapper,
#contact .Contact-innerWrapper{
  padding: 75px 30px
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav,
nav ul,
nav ul li {
    display: flex;
}

nav p,
nav li {
    font-size: 18px;
    text-align: left;
    padding: 15px 15px;
}

#main-nav{
    background-color: #464646;
    position: fixed;
    width: 100%;
}
nav {
    max-width: calc(940px + 30px);
    margin: auto;
    justify-content: space-between;
}
nav ul{
    padding-left: 0px;
}

nav p,
nav ul li a,
nav ul li a i {
    color: white;
    text-decoration: none;
}
#events .Speaker-copy{
    font-size: 16px;
}

#events .Speaker-img {
    width: 90px;
    margin-top: 20px;
}

#events .EventCard-copy p:last-of-type{
    margin-bottom: 30px;
}
#events .EventCard-copy .date:last-of-type{
    margin-bottom: 0px;
}

#events .EventCard-copy .date,
#events .EventCard-copy .location{
    font-size: 19px;
    margin-bottom: 30px;
}


#about .About-copy p{
    margin-top: 0px;
    margin-bottom: 45px;
}
#partner {
    padding: 15px 0px;
}   
#partner .PartnerCard-card {
    position: relative;
    z-index: auto;
}

#partner .PartnerCard-card a {
    position: absolute;
    bottom: 30px;
}
#sponsors .Sponsors-innerWrapper p{
    margin-top: 0px;
}
#contact .Contact-footerLogo {
    margin-bottom: 0px;
}
